// _select.scss
.form-item__dropdown {
  @include display(block);

  position: relative;

  &::after {
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    border-top: 9px solid $color-border-form;
    content: '';
    position: absolute;
    top: 42%;
    right: $space;
    z-index: 2;

    /* These hacks make the select behind the arrow clickable in some browsers */
    pointer-events: none;
    display: none;
  }

  &:hover,
  &:focus {
    &::after {
      border-left: 5px solid transparent;
      border-right: 5px solid transparent;
      border-top: 9px solid $color-border-form-hover;
      content: '';
      position: absolute;
      top: 42%;
      right: $space;
      z-index: 2;

      /* These hacks make the select behind the arrow clickable in some browsers */
      pointer-events: none;
      display: none;
    }
  }
}

select {
  &.form-item__select {
    @include rem(font-size, $font-size-base);
    @include display(inline-block);
    @include rem(margin, 0);

    cursor: pointer;
    border: $border-form-width $border-form $color-border-form;
    outline: none;
    width: 100%;

    &:focus,
    &:hover {
      outline: none;
      color: $color-form-item;
      border: $border-form-width $border-form $color-border-form-focus;
      cursor: pointer;
    }
  }
}

@supports ( -webkit-appearance: none ) or ( appearance: none ) or ( ( -moz-appearance: none ) and ( mask-type: alpha ) ) {
  /* Show custom arrow */
  .form-item__dropdown::after {
    @include display(block);
  }

  .form-item__select {
    /* inside @supports so that iOS <= 8 display the native arrow */
    background: none;

    /* inside @supports so that Android <= 4.3 display the native arrow */
    border: 1px $border-form $transparent;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    &:focus {
      border-radius: 0;
      border: 1px $border-form $transparent;
    }
  }
}
