$table-gray: #f8f8f8;

.table {
  border: 1px solid $color-2;
  border-radius: 4px;
  border-spacing: 0;
  background-color: $table-gray;
  width: 100%;
}

.table__heading-cell {
  border: none;
  border-left: 1px solid $color-2;
  color: $black;
  font-weight: 700;
  padding: 1.2em;
  text-align: left;

  &:first-child {
    border: none;
  }
}

.table__row {
  &:nth-child(odd) {
    background-color: $white;
  }

  // Non-BEM but always contained
  &:last-child {
    td:first-child {
      border-radius: 0 0 0 5px;
    }

    td:last-child {
      border-radius: 0 0 5px;
    }
  }
}

.table__cell {
  border: 1px solid $color-2;
  border-left: none;
  border-bottom: none;
  padding: 1em;

  &:last-child {
    border-right: none;
  }
}

.table__footer-cell,
.table__footer-cell:first-child {
  border-left: none;
  border-top: 1px solid $color-2;
}
