// _buttons.scss
// see: components/_patterns/00-base/_mixins.scss

button {
  @include border-radius($button-border-radius);
}

.button {
  @include button;

  &--alt {
    @include button-alt;
  }

  &--alt2 {
    @include button-alt2;
  }

  &--full {
    width: 100%;
  }

  &--close {
    @include button-close;
  }

  &--close-invert {
    @include button-close-invert;
  }

  &--message {
    position: absolute;
    top: 0;
    right: 0;
  }
}
