// _image.scss
// mixin: _mixins.scss
.img,
.image,
.image__img {
  @include image;
}

.figure {
  //@include display(inline-block);
  @include rem(padding, 0);

  text-align: $figure-text-align;
  //background-color: $color-background-figure;
  img {
    @include rem(padding, 0);
    @include rem(margin, 0);
  }

  .figcaption {
    @include caption();
  }

  .link--image,
  .link--figure {
    &:focus,
    &:active {
      @include rem(padding, 0);
    }
  }
}
