// _02-main-menu-link.scss

.link__main-menu {
  text-decoration: none;
  width: auto;
  color: $color-main-menu-item;
  background-color: $main-menu-item-background-color;

  &:focus,
  &:active {
    @include rem(margin, 0);
    @include rem(padding, 0);

    color: $color-main-menu-item-hover;
    background-color: $main-menu-item-background-color-hover;
    outline-offset: 0;
    outline: 0;

    svg {
      stroke: $transparent;
      fill: $color-main-menu-item-hover;
    }
  }
}

.link-icon__menu-expand {
  @include breakpoint($main-menu-medium) {
    float: none;
  }

  float: right;

  @include rem(width, 32px);
  @include rem(height, 20px);

  //&.toggled {
  //
  //}
}

.main-menu__sub-4--item {
  .link__main-menu {
    @include rem(margin, $menu-sub-item-margin-top $menu-sub-item-margin-right $menu-sub-item-margin-bottom $menu-sub-item-margin-left);
    @include rem(padding, $menu-sub-item-padding-top $menu-sub-item-padding-right $menu-sub-item-padding-bottom $menu-sub-item-padding-left + $space-double);

    &:focus {
      @include rem(margin, $menu-sub-item-margin-top $menu-sub-item-margin-right $menu-sub-item-margin-bottom $menu-sub-item-margin-left);
      @include rem(padding, $menu-sub-item-padding-top $menu-sub-item-padding-right $menu-sub-item-padding-bottom $menu-sub-item-padding-left + $space-double);

      outline-offset: -$border-button-width;
    }
  }
}
