// _radio.scss

.form-item--radio,
.form-item--radio__item {
  @include list-reset;
}

.form-item--radio__item {
  @include display(flex);
  @include align-items(flex-start);
  @include rem(margin, $form-item-base-margin-top $form-item-base-margin-right $form-item-base-margin-bottom $form-item-base-margin-left);
  @include rem(padding, $form-item-base-padding-top $form-item-base-padding-right $form-item-base-padding-bottom $form-item-base-padding-left);
}

.form-item__label {
  &--radio {
    @include rem(font-size, $font-size-label-radio-base);
    @include flex-direction(row);
    @include rem(margin, $label-radio-base-margin-top $label-radio-base-margin-right $label-radio-base-margin-bottom $label-radio-base-margin-left);
    @include rem(padding, $label-radio-base-padding-top $label-radio-base-padding-right $label-radio-base-padding-bottom $label-radio-base-padding-left);

    color: $color-form-label-radio;
  }
}

input[type="radio"] {
  &.radio {
    @include rem(margin, $radio-base-margin-top $radio-base-margin-right $radio-base-margin-bottom $radio-base-margin-left);
    @include rem(padding, $radio-base-padding-top $radio-base-padding-right $radio-base-padding-bottom $radio-base-padding-left);
  }
}
