.tabs__tab,
.link--tabs {
  @include button();
  @include rem(margin, 0 -1px 0 0);
}

.link--tabs-active {
  background-color: $color-btn-bgrnd-hover;
  color: $color-btn-text-hover !important;
  border-style: $border-button;
  border-color: $color-btn-bgrnd-hover;
  border-width: $border-button-width;
  text-decoration: $text-decoration-button-hover;

  &:hover {
    background-color: $color-btn-bgrnd;
    color: $color-btn-text;
    border-style: $border-button;
    border-color: $color-btn-bgrnd;
    border-width: $border-button-width;
  }
}

.tabs__content {
  display: none;

  &--js-active {
    @include display(block);
  }

  &--login,
  &--register,
  &--reset-pw {
    @include rem(padding, $space 0 0 0);
  }
}

// on user login, register, reset password page
.link--tabs-small {
  @include rem(font-size, $small);
  @include rem(padding, $space-top-button $space-right-button/2 $space-bottom-button $space-left-button/2);
}
