// _devhelpers.scss

$bp-xs-dev-color: red;
$bp-small-dev-color: green;
$bp-medium-dev-color: blue;
$bp-large-dev-color: orange;
$bp-xl-dev-color: magenta;

/// show breakpoint infos
body {
  &:not(.cke_editable) {
    @include xs {
      &::before {
        @include rem(top, $main-menu-height);

        content: "EXTRASMALL";
        display: block;
        background-color: $bp-xs-dev-color;
        position: fixed;
        z-index: 99999;
        right: 0;
      }
    }

    @include small {
      &::before {
        @include rem(top, $main-menu-height);

        content: "SMALL";
        display: block;
        background-color: $bp-small-dev-color;
        position: fixed;
        z-index: 99999;
        right: 0;
      }
    }

    @include medium {
      &::before {
        @include rem(top, $main-menu-height);

        content: "MEDIUM";
        display: block;
        background-color: $bp-medium-dev-color;
        position: fixed;
        z-index: 99999;
        right: 0;
      }
    }

    @include large {
      &::before {
        @include rem(top, $main-menu-height);

        content: "LARGE";
        display: block;
        background-color: $bp-large-dev-color;
        z-index: 99999;
        right: 0;
      }
    }

    @include xl {
      &::before {
        @include rem(top, $main-menu-height);

        content: "EXTRALARGE";
        display: block;
        background-color: $bp-xl-dev-color;
        position: fixed;
        z-index: 99999;
        right: 0;
      }
    }
  }
}

