// _checkboxes.scss

.form-item--checkboxes {
  @include rem(margin-bottom, 0);
}

.form-item--checkbox__item {
  @include display(flex);
  @include align-items(flex-start);
  @include rem(margin, $form-item-base-margin-top $form-item-base-margin-right $form-item-base-margin-bottom $form-item-base-margin-left);
  @include rem(padding, $form-item-base-padding-top $form-item-base-padding-right $form-item-base-padding-bottom $form-item-base-padding-left);

  &:last-child {
    @include rem(margin-bottom, 0);
  }
}

.form-item__label {
  &--checkbox {
    @include rem(font-size, $font-size-label-checkbox-base);
    @include flex-direction(row);
    @include rem(margin, $label-checkbox-base-margin-top $label-checkbox-base-margin-right $label-checkbox-base-margin-bottom $label-checkbox-base-margin-left);
    @include rem(padding, $label-checkbox-base-padding-top $label-checkbox-base-padding-right $label-checkbox-base-padding-bottom $label-checkbox-base-padding-left);

    font-family: $font-family-label;
    font-weight: $font-weight-label-checkbox;
    color: $color-form-label-checkbox;
  }
}

input[type="checkbox"] {
  &.checkbox {
    @include rem(margin, $checkbox-base-margin-top $checkbox-base-margin-right $checkbox-base-margin-bottom $checkbox-base-margin-left);
    @include rem(padding, $checkbox-base-padding-top $checkbox-base-padding-right $checkbox-base-padding-bottom $checkbox-base-padding-left);
  }
}
