//_00-main-menu.sccss

/// Menu Breakpoint
// see: components/_patterns/00-base/_breakpoints.scss/

.navigation--main {
  background-color: $main-menu-background-color;
}

.main-menu {
  @include rem(height, $main-menu-height + 1);
  @include rem(margin, 0);
  @include rem(padding, 0);

  width: 100%;
  color: $color-main-menu;
  background-color: $main-menu-background-color;
  z-index: 10;

  @include breakpoint($main-menu-medium) {
    @include display(block);
  }

  @include display(none);
}

// small devices: open menu
.js-nav-active {
  .opened {
    .main-menu {
      @include display(block);
    }
  }
}
// small devices: submenu items active
.js-nav-active {
  .main-menu__sub {
    &.toggled {
      @include display(block);
    }
  }
}

.main-menu {
  .main-menu__sub {
    @include breakpoint($main-menu-medium) {
      position: absolute;
    }

    @include display(none);
    @include rem(margin, 0 0 0 0);
    @include rem(padding, 0 0 0 0);
    @include rem(left, 0);

    background-color: $main-menu-background-color-sub;
    position: relative;
  }
}

.main-menu__sub--item-expanded {
  .link__main-menu {
    @include rem(margin-right, 24px);
    @include display(inline-block);

    width: 100%;
  }

  .link-icon__menu-expand {
    position: absolute;
    right: 0;
  }
}

// submenu colorized
.main-menu__sub {
  &.sub-1 {
    .main-menu__sub--item {
      background-color: darken($main-menu-background-color, 10%);

      .link__main-menu {
        background-color: darken($main-menu-background-color, 10%);
      }
    }
  }

  &.sub-2 {
    .main-menu__sub--item {
      background-color: darken($main-menu-background-color, 15%);

      .link__main-menu {
        background-color: darken($main-menu-background-color, 15%);
      }
    }
  }

  &.sub-3 {
    .main-menu__sub--item {
      background-color: darken($main-menu-background-color, 20%);

      .link__main-menu {
        background-color: darken($main-menu-background-color, 20%);
      }
    }
  }

  &.sub-4 {
    .main-menu__sub--item {
      background-color: darken($main-menu-background-color, 25%);

      .link__main-menu {
        background-color: darken($main-menu-background-color, 25%);
      }
    }
  }
}
