//_lists.scss
// variables are here: custom/crispy/components/_patterns/00-base/_01-variables.scss
ul,
ol {
  list-style-position: inside;
}

ul {
  @include rem(margin, $ul-margin-top $ul-margin-right $ul-margin-bottom $ul-margin-left);
  @include rem(padding, $ul-padding-top $ul-padding-right $ul-padding-bottom $ul-padding-left);

  list-style-type: $ul-list-style-type;
  background-color: $ul-background-color;
}

ol {
  @include rem(margin, $ol-margin-top $ol-margin-right $ol-margin-bottom $ol-margin-left);
  @include rem(padding, $ol-padding-top $ol-padding-right $ol-padding-bottom $ol-padding-left);

  list-style-type: $ol-list-style-type;
  background-color: $ol-background-color;
}

.list {
  @include rem(margin, $ul-margin-top $ul-margin-right $ul-margin-bottom $ul-margin-left);
  @include rem(padding, $ul-padding-top $ul-padding-right $ul-padding-bottom $ul-padding-left);

  &__nostyle {
    list-style-type: none;
  }

  &__disc {
    list-style-type: disc;
  }

  &__circle {
    list-style-type: circle;
  }

  &__square {
    list-style-type: square;
  }

  &__decimal {
    list-style-type: decimal;
  }

  &__list-item {
    @include rem(margin, $li-margin-top $li-margin-right $li-margin-bottom $li-margin-left);
    @include rem(padding, $li-padding-top $li-padding-right $li-padding-bottom $li-padding-left );
  }

  &--inline {
    li {
      @include display(inline-block);
      @include rem(margin, $li-inline-margin-top $li-inline-margin-right $li-inline-margin-bottom $li-inline-margin-left);
      @include rem(padding, $li-inline-padding-top $li-inline-padding-right $li-inline-padding-bottom $li-inline-padding-left);
    }
  }
}

.list__list-item--icon {
  @include display(flex);
}

.list-icon__content {
  @include flex-direction(row);
}
