// _status.scss

.message {
  position: relative;
  width: 100%;

  .link,
  a {
    color: $color-text;
  }
}

@each $status-type, $color in $status-background-colors {
  .message__#{$status-type} {
    @include display(flex);
    @include justify-content(center);
    @include rem(margin, $status-base-margin-top $status-base-margin-right $status-base-margin-bottom $status-base-margin-left);
    @include rem(padding, $status-base-padding-top $status-base-padding-right $status-base-padding-bottom $status-base-padding-left);

    font-weight: $status-font-weight;
    font-style: $status-font-style;
    text-decoration: $text-decoration-status;
    background-color: $color;
    text-align: center;
    border-top: $border-message-width $border-message darken($color, 15%);
    border-right: $border-message-width $border-message darken($color, 15%);
    border-bottom: $border-message-width $border-message darken($color, 15%);
    border-left: $space-one-half $border-message darken($color, 15%);
  }
}

@each $status-type, $color in $status-colors {
  .message__#{$status-type} {
    color: $color;
  }
}

@each $status-type, $color in $status-background-colors {
  .message__#{$status-type} {
    .button--message {
      background-color: darken($color, 15%);

      &:hover {
        background-color: $color;
      }
    }
  }
}
