// _pager.scss

.pager {
  @include rem(margin, $pager-base-margin-top $pager-base-margin-right $pager-base-margin-bottom $pager-base-margin-left);
  @include rem(padding, $pager-base-padding-top $pager-base-padding-right $pager-base-padding-bottom $pager-base-padding-left);
  @include display(flex);
  @include justify-content(center);

  width: 100%;
}

.pager__items {
  @include list-reset;

  text-align: center;
}

.pager__item {
  @include display(inline-block);
  @include rem(margin, $pager-item-base-margin-top $pager-item-base-margin-right $pager-item-base-margin-bottom $pager-item-base-margin-left);
  @include rem(padding, $pager-item-base-padding-top $pager-item-base-padding-right $pager-item-base-padding-bottom $pager-item-base-padding-left);

  &:last-of-type {
    @include rem(margin, 0);
    @include rem(padding, 0);
  }
}

.pager__link {
  color: $color-pager-base;
  font-weight: $pager-font-weight;
  font-style: $pager-font-style;
  text-decoration: $text-decoration-pager;

  &.is-active,
  &:hover,
  &:active,
  &:focus {
    color: $color-pager-base-hover;
    text-decoration: $text-decoration-pager-hover;
  }
}

.pager__link--next,
.pager__link--prev {
  @include display(block);

  svg {
    @include rem(height, $icon-pager-base-height);
    @include rem(width, $icon-pager-base-width);

    fill: $color-pager-base;
    stroke: $color-pager-base;
  }

  &:hover {
    svg {
      fill: $color-pager-base-hover;
      stroke: $color-pager-base-hover;
    }
  }
}
