// _flexbox.scss

/// Mixin - flex-direction
@mixin flex-direction($flex-direction: null) {
  flex-direction: $flex-direction !important;
}

.flex-row {
  @include flex-direction(row);
}

.flex-row-reverse {
  @include flex-direction(row-reverse);
}

.flex-column {
  @include flex-direction(column);
}

.flex-column-reverse {
  @include flex-direction(column-reverse);
}

/// Mixin - wrap
@mixin flex-wrap($flex-wrap: null) {
  flex-wrap: $flex-wrap !important;
}

.flex-wrap {
  @include flex-wrap(wrap);
}

.flex-wrap-reverse {
  @include flex-wrap(wrap-reverse);
}

.flex-nowrap {
  @include flex-wrap(nowrap);
}

/// Mixin - justify content
@mixin justify-content($justify-content: null) {
  justify-content: $justify-content !important;
}

.justify-content-start {
  @include justify-content(flex-start);
}

.justify-content-end {
  @include justify-content(flex-end);
}

.justify-content-center {
  @include justify-content(center);
}

.justify-content-between {
  @include justify-content(space-between);
}

.justify-content-around {
  @include justify-content(space-around);
}

/// Mixin - align items
@mixin align-items($align-items: null) {
  align-items: $align-items !important;
}

.align-items-start {
  @include align-items(flex-start);
}

.align-items-end {
  @include align-items(flex-end);
}

.align-items-center {
  @include align-items(center);
}

.align-items-baseline {
  @include align-items(baseline);
}

.align-items-stretch {
  @include align-items(stretch);
}

/// Mixin - align content
@mixin align-content($align-content: null) {
  align-content: $align-content !important;
}

.align-content-start {
  @include align-content(flex-start);
}

.align-content-end {
  @include align-content(flex-end);
}

.align-content-center {
  @include align-content(center);
}

.align-content-between {
  @include align-content(space-between);
}

.align-content-around {
  @include align-content(space-around);
}

.align-content-stretch {
  @include align-content(stretch);
}

/// Mixin - align-self
@mixin align-self($align-self: null) {
  align-self: $align-self !important;
}

.align-self-auto {
  @include align-self(auto);
}

.align-self-start {
  @include align-self(flex-start);
}

.align-self-end {
  @include align-self(flex-end);
}

.align-self-center {
  @include align-self(center);
}

.align-self-baseline {
  @include align-self(baseline);
}

.align-self-stretch {
  @include align-self(stretch);
}
