// _accordion-items.scss

.accordion-term {
  @include display(block);
  @include rem(font-size, $font-size-accordion);
  @include rem(margin, 0 0 $space-one-half 0);
  @include rem(padding, 0 0 0 $space-one-fourth);

  background-color: $color-accordion-background;
  position: relative;
  font-weight: $font-weight-accordion;
  font-style: $accordion-font-style;
  color: $color-accordion;
  cursor: pointer;

  .accordion-icon--show {
    @include display(inline);
  }

  .accordion-icon--hide {
    @include display(none);
  }

  &:hover {
    color: $color-accordion-hover;
    background-color: $color-accordion-background-hover;

    svg {
      fill: $icon-base-accordion-fill-color-hover;
      stroke: $icon-base-link-stroke-color-hover;
    }
  }

  &.js-is-active {
    color: $color-accordion-active;
    background-color: $color-accordion-active-background;

    svg {
      fill: $icon-base-accordion-fill-color-hover;
      stroke: $icon-base-accordion-stroke-color-hover;
    }

    .accordion-icon--show {
      @include display(none);
    }

    .accordion-icon--hide {
      @include display(inline);
    }

    & + dd {
      @include rem(padding, 0 0 0 0);

      height: auto;
      overflow: visible;
    }

    &:hover {
      color: $color-accordion;
      background-color: $color-accordion-background;

      svg {
        fill: $icon-base-accordion-fill-color;
        stroke: $icon-base-accordion-stroke-color;
      }
    }
  }
}

.accordion-def {
  &.js-active {
    height: 0;
    overflow: hidden;
  }
}
