// _03-main-menu-toggle.scss

.mobile-menu {
  @include rem(height, $main-menu-height + 1);
  @include rem(margin, $menu-item-margin-top $menu-item-margin-right $menu-item-margin-bottom $menu-item-margin-left);
  @include rem(padding, $menu-item-padding-top 0 $menu-item-padding-bottom $menu-item-padding-left);

  text-align: right;
  width: 100%;
  color: $color-main-menu;
  background-color: $main-mobile-menu-background-color;
  z-index: 11;
  cursor: pointer;
}

.icon__mobile_menu {
  @include rem(height, $main-menu-height);
  @include rem(width, $icon-base-width);

  svg {
    @include rem(height, $main-menu-height);
    @include rem(width, $icon-base-width);

    fill: $color-main-menu;
    stroke: $transparent;
    overflow: hidden;
  }
}
