// _divider.scss

.divider {
  @include rem(padding,$divider-base-padding-top $divider-base-padding-right $divider-base-padding-bottom $divider-base-padding-left);
  @include rem(margin,$divider-base-margin-top $divider-base-margin-right $divider-base-margin-bottom $divider-base-margin-left);
  @include rem(font-size, $font-size-border-base);
  @include rem(height, $divider-base-height);

  width: 100%;
  text-align: $divider-inner-align;
  border-top-style: $border-divider;
  border-color: $color-border-divider;
  border-width: $border-divider-width;
  line-height: $divider-line-height;
  font-weight: $font-weight-border-base;

  &__content {
    @include display(inline-flex);
    @include justify-content(center);
    @include flex-wrap(wrap);

    width: auto;
    transform: translateY(-50%);
  }
}

.divider-inner {
  @include rem(padding,$divider-inner-padding-top $divider-inner-padding-right $divider-inner-padding-bottom $divider-inner-padding-left);
  @include rem(margin,$divider-inner-margin-top $divider-inner-margin-right $divider-inner-margin-bottom $divider-inner-margin-left);
  @include display(inline-block);

  white-space: nowrap;
  line-height: $divider-inner-line-height;
  background-color: $color-background-divider-inner;

  svg {
    fill: $icon-base-divider-fill-color;
    stroke: $icon-base-divider-fill-color;
    //&:hover {
    //  fill: $icon-base-divider-fill-color-hover;
    //  stroke: $icon-base-divider-fill-color-hover;
    //}
  }
}
