// _status_overlay.scss

.message-wrapper {
  z-index: 4;
  background-color: rgba(22, 25, 25, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: fadeinwrapper 0.8s;
}

.message--overlay {
  z-index: 3;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  animation: fadeinmessage 0.8s;

  @include medium {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
  }
}

@keyframes fadeinwrapper {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeinmessage {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
