// _position.scss

/// Mixins -  position

@mixin position($position, $top: null, $right: null, $bottom: null, $left: null) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left;
}

// Position relative
.relative {
  @include position(relative);
}

// Position absolute
.absolute {
  @include position(absolute);
}

// Position fixed
.fixed {
  @include position(fixed);
}

/// Mixins - float
@mixin float-left {
  float: left !important;
}

@mixin float-right {
  float: right !important;
}

@mixin float-none {
  float: none !important;
}

// Float left
.float-left {
  @include float-left;
}

// Float right
.float-right {
  @include float-right;
}

// Float none
.float-none {
  @include float-none;
}

