// _site-main.scss

.main {
  //@include wrapper;
  @include rem(padding-left, $space-one-third);
  @include rem(padding-right, $space-one-third);

  @include bp(xl) {
    @include rem(padding-left, 0);
    @include rem(padding-right, 0);
  }

  overflow: hidden;
  //background: $color-background-main;
}

// user login, register, password reset
.main__user-register-overlay-wrapper {
  z-index: 3;
  background-color: rgba(22, 25, 25, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: fadeinwrapper 0.9s;
}

.main__user-register--overlay {
  @include rem(padding, $space);

  background-color: $white;
  z-index: 4;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  animation: fadeinmessage 0.7s;

  @include medium {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
  }
}

// close button (login, register, and pwreset page)
.button--close-login,
.button--close-register,
.button--close-pwreset {
  @include rem(top, -$space-double);
  @include rem(right, -$space-double);

  position: absolute;
}
