// _01-main-menu-item.scss

.main-menu__item {
  @include breakpoint($main-menu-medium) {
    @include display(inline);
    @include float-left;
    @include rem(margin, $menu-item-margin-top $menu-item-margin-right $menu-item-margin-bottom $menu-item-margin-left);
    @include rem(padding, $menu-item-padding-top $menu-item-padding-right $menu-item-padding-bottom $menu-item-padding-left);

    width: auto;
  }

  @include display(block);
  @include float-left;
  @include rem(padding, $menu-item-padding-top 0 $menu-item-padding-bottom 0);

  width: 100%;
  position: relative;
  color: $color-main-menu-item;
  background-color: $main-menu-item-background-color;

  svg {
    @include rem(height, $main-menu-height);

    stroke: $transparent;
    fill: $color-main-menu-item;
  }
}

.main-menu__item--active-trail {
  &:hover,
  &:focus {
    color: $color-main-menu-item;
    background-color: $main-menu-item-background-color;

    svg {
      stroke: $transparent;
      fill: $color-main-menu-item;
    }

    > .link__main-menu {
      color: $color-main-menu-item;
      background-color: $main-menu-item-background-color;
    }
  }
}

// sub menu item
.main-menu__sub {
  .main-menu__sub--item {
    @include breakpoint($main-menu-medium) {
      @include rem(margin, $menu-sub-item-margin-top $menu-sub-item-margin-right $menu-sub-item-margin-bottom $menu-sub-item-margin-left);
      @include rem(padding, 0);
    }

    @include rem(margin, $menu-sub-item-margin-top $menu-sub-item-margin-right $menu-sub-item-margin-bottom $menu-sub-item-margin-left);
    @include rem(padding, 0);
    @include float-left;

    position: relative;
    color: $color-main-menu-sub;
    background-color: $main-menu-background-color-sub;
    white-space: nowrap;
    width: 100%;

    svg {
      @include rem(height, $main-menu-height);

      stroke: $transparent;
      fill: $color-main-menu-sub;
    }

    .link__main-menu {
      @include rem(padding, $menu-sub-item-padding-top $menu-sub-item-padding-right $menu-sub-item-padding-bottom $menu-sub-item-padding-left);
    }

    &:hover,
    &:focus,
    &:active,
    &.main-menu__item--active-trail,
    &.main-menu__sub--item-active-trail {
      cursor: pointer;
      color: $color-main-menu-sub-hover;
      background-color: $main-menu-background-color-sub-hover;

      svg {
        stroke: $transparent;
        fill: $color-main-menu-sub-hover;
      }

      > .link__main-menu {
        color: $color-main-menu-sub-hover;
        background-color: $main-menu-background-color-sub-hover;
        text-decoration: underline;
      }

      &.main-menu__item--active-trail,
      &.main-menu__sub--item-active-trail {
        a {
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}

.main-menu__item--expanded {
  &:hover {
    .main-menu__sub {
      &.sub-1 {
        @include breakpoint($main-menu-medium) {
          @include display(block);

          width: auto;
        }

        @include display(inline-block);

        width: 100%;
      }
    }
  }
}

.main-menu__sub {
  &.sub-1 {
    .main-menu__sub--item-expanded {
      &:hover {
        .main-menu__sub {
          &.sub-2 {
            @include breakpoint($main-menu-medium) {
              @include display(block);

              position: absolute;
              left: 100%;
              top: 0;
              width: auto;
            }

            @include display(block);

            width: 100%;
            position: relative;
          }
        }
      }
    }
  }
}

.main-menu__sub {
  &.sub-2 {
    .main-menu__sub--item {
      svg {
        stroke: $transparent;
        fill: $color-main-menu-sub;
      }

      &:hover {
        svg {
          stroke: $transparent;
          fill: $color-main-menu-sub-hover;
        }
      }
    }

    .main-menu__sub--item-expanded {
      &:hover {
        .main-menu__sub {
          &.sub-3 {
            @include breakpoint($main-menu-medium) {
              @include display(block);

              position: absolute;
              left: 100%;
              top: 0;
              width: auto;
            }

            @include display(block);

            width: 100%;
            position: relative;

            svg {
              stroke: $transparent;
              fill: $color-main-menu-sub;
            }

            &:hover {
              svg {
                stroke: $transparent;
                fill: $color-main-menu-sub-hover;
              }
            }
          }
        }
      }
    }
  }
}

.main-menu__sub {
  &.sub-3 {
    .main-menu__sub--item {
      svg {
        stroke: $transparent;
        fill: $color-main-menu-sub;
      }

      &:hover {
        svg {
          stroke: $transparent;
          fill: $color-main-menu-sub-hover;
        }
      }
    }

    .main-menu__sub--item-expanded {
      &:hover {
        .main-menu__sub {
          &.sub-4 {
            @include breakpoint($main-menu-medium) {
              @include display(block);

              position: absolute;
              left: 100%;
              top: 0;
              width: auto;
            }

            @include display(block);

            width: 100%;
            position: relative;

            svg {
              stroke: $transparent;
              fill: $color-main-menu-sub;
            }

            &:hover {
              svg {
                stroke: $transparent;
                fill: $color-main-menu-sub-hover;
              }
            }
          }
        }
      }
    }
  }
}

// expanded on click - see /components/_patterns/02-molecules/menus/main-menu/main_menu.js
.main-menu {
  .main-menu__sub {
    &.js-main-menu__sub--open {
      &.sub-1 {
        @include breakpoint($main-menu-medium) {
          @include display(block);

          width: auto;
        }

        @include display(inline-block);

        width: 100%;
      }

      &.sub-2,
      &.sub-3,
      &.sub-4 {
        @include breakpoint($main-menu-medium) {
          @include display(block);

          position: absolute;
          left: 100%;
          top: 0;
          width: auto;
        }

        @include display(block);

        width: 100%;
        position: relative;
      }
    }
  }
}

// small devices: open menu
.js-nav-active {
  //.main-menu__item--expanded {
  //
  //}

  .link-icon__menu-expand {
    @include breakpoint($main-menu-medium) {
      @include rem (margin, 0 0 0 $icon-link-base-margin-left);

      text-align: right;
    }

    @include rem (margin, 0);

    text-align: center;
  }
}
