// _teaser.scss
.link {
  &--teaser {
    @include display(flex);
    @include flex-direction(column);

    text-decoration: none;
    height: auto;

    &:hover {
      color: $color-link-base-hover;

      .heading,
      .paragraph,
      .link--readmore {
        color: $color-link-base-hover;
      }
    }
  }
}

.paragraph--teaser {
  flex-grow: 1;
}

