// _site-footer.scss

.footer {
  @include rem(padding, $footer-base-padding-top $footer-base-padding-right $footer-base-padding-bottom $footer-base-padding-left);
  @include rem(margin-top, $footer-base-margin-top);
  @include rem(margin-bottom, $footer-base-margin-bottom);

  background: $color-background-footer;
  width: 100%;
}

.footer__inner {
  @include wrapper;
}

