// _align.scss

/// Mixin -  align

@mixin align($align: null) {
  vertical-align: $align !important;
}

.align-baseline {
  @include align(baseline);
}

.align-top {
  @include align(top);
}

.align-middle {
  @include align(middle);
}

.align-bottom {
  @include align(bottom);
}

.align-text-bottom {
  @include align(text-bottom);
}

.align-text-top {
  @include align(text-top);
}
