// readmore-link.scss
/// Link - see components/_patterns/00-base/_mixins.scss
.link {
  &--readmore {
    color: $color-link-readmore;
    background-color: $color-link-readmore-background;

    svg {
      fill: $icon-readmore-link-fill-color;
      stroke: $icon-readmore-link-stroke-color;
      overflow: hidden;
    }

    &:hover,
    &:active {
      color: $color-link-readmore-hover;
      background-color: $color-link-readmore-hover-background;

      svg {
        fill: $icon-readmore-link-fill-color-hover;
        stroke: $icon-readmore-link-stroke-color-hover;
        overflow: hidden;
      }
    }

    &:focus {
      color: $color-link-readmore-focus;
      background-color: $color-link-readmore-focus-background;
    }
  }
}

.link-icon {
  &__readmore {
    @include rem(margin, $icon-readmore-margin-top $icon-readmore-margin-right $icon-readmore-margin-bottom $icon-readmore-margin-left);
    @include rem(padding, $icon-readmore-padding-top $icon-readmore-padding-right $icon-readmore-padding-bottom $icon-readmore-padding-left);
  }
}
