  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  color: #161919;
  text-align: left;
  background-color: white;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

