  font-weight: bolder;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 0.75rem;
  position: relative;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -4px;
}

sup {
  top: -4px;
}

a {
  color: #e34b42;
  text-decoration: underline;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #161919;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
