@keyframes fadeinmessage {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.link--teaser {
  display: flex !important;
  flex-direction: column !important;
  text-decoration: none;
  height: auto;
}

.link--teaser:hover {
  color: #161919;
}

.link--teaser:hover .heading,
.link--teaser:hover .paragraph,
.link--teaser:hover .link--readmore {
  color: #161919;
}

.paragraph--teaser {
  flex-grow: 1;
}

.accordion {
  margin: 0 0 16px 0;
  margin: 0 0 1rem 0;
}

.header {
  background-color: transparent;
}

.header .main-menu,
.header .breadcrumb {
  margin: 0 auto;
  max-width: 75em;
}

.header .main-menu::after,
.header .breadcrumb::after {
  clear: both;
  content: "";
  display: block;
}

.main {
  padding-left: 5.33333px;
  padding-left: 0.33333rem;
  padding-right: 5.33333px;
  padding-right: 0.33333rem;
  overflow: hidden;
}

@media (min-width: 75em) {
  .main {
    padding-left: 0;
    padding-left: 0;
    padding-right: 0;
    padding-right: 0;
  }
}

.main__user-register-overlay-wrapper {
  z-index: 3;
  background-color: rgba(22, 25, 25, 0.9);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  animation: fadeinwrapper 0.9s;
}

.main__user-register--overlay {
  padding: 16px;
  padding: 1rem;
  background-color: white;
  z-index: 4;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  animation: fadeinmessage 0.7s;
}

@media (min-width: 41em) {
  .main__user-register--overlay {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
  }
}

.button--close-login,
.button--close-register,
.button--close-pwreset {
  top: -32px;
  top: -2rem;
  right: -32px;
  right: -2rem;
  position: absolute;
}

.footer {
  padding: 16px 0 16px 0;
  padding: 1rem 0 1rem 0;
  margin-top: 16px;
  margin-top: 1rem;
  margin-bottom: 0;
  margin-bottom: 0;
  background: #161919;
  width: 100%;
}

.footer__inner {
  margin: 0 auto;
  max-width: 75em;
}

.footer__inner::after {
  clear: both;
  content: "";
  display: block;
}

