// _forms.scss
// variables are here: components/_patterns/00-base/_01-variables.scss
// colors are here: components/_patterns/00-base/global/01-colors/_colors-used.scss

// if input has an icon before or after change the padding according to the form icon size
$padding-adaption: $space-form-icon-after - $space;

// border radius, width
input[type="text"],
textarea,
select,
input[type="search"],
input[type="number"],
input[type="password"],
input[type="url"],
input[type="email"],
input[type="tel"],
input[type="date"] {
  @include border-radius($button-border-radius);

  width: 100%;
}

select {
  border-radius: $button-border-radius;
}

.form-item {
  @include rem(margin, $form-item-base-margin-top $form-item-base-margin-right $form-item-base-margin-bottom $form-item-base-margin-left);
  @include rem(padding, $form-item-base-padding-top $form-item-base-padding-right $form-item-base-padding-bottom $form-item-base-padding-left);
  @include clearfix;
  @include rem(max-width, $form-max-width);

  position: relative;
  color: $color-form-item;
  background-color: $color-form-item-background;

  // if input has a form icon before
  &--icon-before {
    .form-item-icon {
      @include rem(font-size, $small);
      @include rem(width, $space-form-icon-before);
      @include rem(height, $space-form-icon-before);
      @include display(flex);
      @include justify-content(center);
      @include align-items(center);

      position: absolute;
      bottom: $border-form-width;
      left: $border-form-width;
      z-index: 2;
      overflow: hidden;

      svg {
        @include rem(height, $icon-form-base-height);
        @include rem(width, $icon-form-base-width);

        fill: $color-icon-form;
        overflow: hidden;
        //stroke: $color-icon-form;
      }
    }
  }
  // if input has a form icon after
  &--icon-after {
    .form-item-icon {
      @include rem(font-size, $small);
      @include rem(width, $space-form-icon-after);
      @include rem(height, $space-form-icon-after);
      @include display(flex);
      @include justify-content(center);
      @include align-items(center);

      position: absolute;
      bottom: $border-form-width;
      right: $border-form-width;
      z-index: 2;
      overflow: hidden;

      svg {
        @include rem(height, $icon-form-base-height);
        @include rem(width, $icon-form-base-width);

        overflow: hidden;
        fill: $color-icon-form;
        //stroke: $color-icon-form;
      }
    }
  }

  &--required {
    .form-item-icon {
      svg {
        fill: $color-form-required;
      }
    }
  }
}

.legend {
  @include rem(font-size, $font-size-legend-base);

  font-family: $font-family-legend;
  font-weight: $font-weight-legend-base;
  font-style: $font-style-legend-base;
  color: $color-form-item;
  background-color: $color-form-item-background;
}

.form-item__label {
  @include display(inline-block);

  @extend .legend;
}

label {
  @include display(inline-block);

  padding-bottom: 0;
}
// input margin, padding
input[type="text"],
input[type="search"],
input[type="number"],
input[type="password"],
input[type="url"],
input[type="email"],
input[type="tel"],
input[type="date"],
select,
textarea {
  @include rem(margin, $form-base-margin-top $form-base-margin-right $form-base-margin-bottom $form-base-margin-left);
  @include rem(padding, $form-base-padding-top $form-base-padding-right $form-base-padding-bottom $form-base-padding-left);
}
// if input has an icon before or after change the padding depending on the icon size ($space-form-icon-after, $space-form-icon-before)
.form-item {
  &--icon-after {
    input[type="text"],
    input[type="search"],
    input[type="number"],
    input[type="password"],
    input[type="url"],
    input[type="email"],
    input[type="tel"],
    input[type="date"],
    select {
      @include rem(padding, $form-base-padding-top $form-base-padding-right + $space-form-icon-after - $space $form-base-padding-bottom $form-base-padding-left);
    }
  }

  &--icon-before {
    input[type="text"],
    input[type="search"],
    input[type="number"],
    input[type="password"],
    input[type="url"],
    input[type="email"],
    input[type="tel"],
    input[type="date"],
    select {
      @include rem(padding, $form-base-padding-top $form-base-padding-right $form-base-padding-bottom $form-base-padding-left + $space-form-icon-before - $space);
    }
  }
}

.form-item__description {
  @include rem(padding, $form-item-description-padding-top $form-item-description-padding-right $form-item-description-padding-bottom $form-item-description-padding-left);
  @include rem(padding, $form-item-description-margin-top $form-item-description-margin-right $form-item-description-margin-bottom $form-item-description-margin-left);
  @include rem(font-size, $font-size-description-base);

  //position: absolute;
  //left: 0;
  color: $color-form-description;
  background-color: $color-form-description-background;
}

.form-item__error {
  @include rem(padding, $form-item-error-padding-top $form-item-error-padding-right $form-item-error-padding-bottom $form-item-error-padding-left);
  @include rem(padding, $form-item-error-margin-top $form-item-error-margin-right $form-item-error-margin-bottom $form-item-error-margin-left);
  @include rem(font-size, $font-size-error-base);

  position: absolute;
  left: 0;
  color: $color-form-error;
  background-color: $color-form-error-background;
  width: 100%;
  text-align: right;
}

.form-item__success {
  @include rem(padding, $form-item-error-padding-top $form-item-error-padding-right $form-item-error-padding-bottom $form-item-error-padding-left);
  @include rem(padding, $form-item-error-margin-top $form-item-error-margin-right $form-item-error-margin-bottom $form-item-error-margin-left);
  @include rem(font-size, $font-size-error-base);

  position: absolute;
  left: 0;
  color: $color-form-success;
  background-color: $color-form-success-background;
  width: 100%;
  text-align: right;
}

.form-item__required {
  @include rem(padding, $form-item-error-padding-top $form-item-error-padding-right $form-item-error-padding-bottom $form-item-error-padding-left);
  @include rem(padding, $form-item-error-margin-top $form-item-error-margin-right $form-item-error-margin-bottom $form-item-error-margin-left);
  @include rem(font-size, $font-size-error-base);

  position: absolute;
  left: 0;
  color: $color-form-required;
  background-color: $color-form-required-background;
  width: 100%;
  text-align: right;
}

.form-fieldset {
  @include rem(margin-bottom, $space);
  @include rem(padding, 0);

  border: none;

  .form-item {
    @include rem(margin-left, $form-item-base-margin-left);
  }
}
// last fieldset in a form
form {
  .form-fieldset {
    &:last-of-type {
      @include rem(margin-bottom, $space-one-third);
    }
  }
}

// style input submit like a button
input[type="submit"] {
  @extend .button;
}

input[type="text"],
input[type="search"],
input[type="number"],
input[type="password"],
input[type="url"],
input[type="email"],
input[type="tel"],
input[type="date"] {
  &:hover {
    cursor: pointer;
  }

  &:hover,
  &:focus,
  &:active {
    ~ .form-item-icon {
      svg {
        fill: $color-icon-form-focus;
      }
    }
  }
}

.asterix {
  @include rem(font-size, $small);
  @include rem(margin-left, $space-one-third);

  position: relative;
  top: -$space-one-fourth;
  line-height: 0;
  vertical-align: baseline;

  &--mandatory {
    color: $mandatory;
  }
}

// success
.form-item-icon {
  &__success {
    svg,
    path {
      //stroke: $color-form-success-icon;
      fill: $color-form-success-icon;
    }
  }

  &__required {
    svg,
    path,
    g {
      //stroke: $color-form-required-icon;
      fill: $color-form-required-icon;
    }
  }

  &__error {
    svg,
    path {
      //stroke: $color-form-error-icon;
      fill: $color-form-error-icon;
    }
  }
}

// form validation in browser

:not(output):-moz-ui-invalid {
  border-color: $color-form-required-error;
  background-color: $color-form-input-required-background;
  outline: none;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

