// _icon.scss

.icon {
  @include rem(height, $icon-base-height);
  @include rem(width, $icon-base-width);
  @include align(middle);
  @include display(inline-block);

  overflow: hidden;
  background: $icon-base-background-color;

  svg {
    @include rem(height, $icon-base-height);
    @include rem(width, $icon-base-width);

    fill: $icon-base-fill-color;
    stroke: $icon-base-stroke-color;
    overflow: hidden;
  }

  &--left {
    @include display(inline-block);
  }

  &--right {
    @include display(inline-block);
  }
}

.button-icon {
  @include display(flex);
  @include justify-content(center);
  @include align-items(center);
  @include rem(height, $icon-button-base-height);
  @include rem(width, $icon-button-base-width);

  svg {
    @include rem(height, $icon-button-base-height);
    @include rem(width, $icon-button-base-width);

    fill: $icon-base-fill-color;
    stroke: $icon-base-stroke-color;
    overflow: hidden;
  }

  &--before {
    @include float-left;
    @include rem (margin, 0 $icon-button-base-margin-right 0 0);
  }

  &--after {
    @include float-right;
    @include rem (margin, 0 0 0 $icon-button-base-margin-left);
  }

  &--alt {
    @include rem(height, $font-size-button-alt);
    @include rem(width, $font-size-button-alt);

    svg {
      @include rem(height, $font-size-button-alt);
      @include rem(width, $font-size-button-alt);
    }
  }

  &--alt2 {
    @include rem(height, $font-size-button-alt2);
    @include rem(width, $font-size-button-alt2);

    svg {
      @include rem(height, $font-size-button-alt2);
      @include rem(width, $font-size-button-alt2);
    }
  }
}

.link-icon {
  @include display(flex);
  @include justify-content(center);
  @include align-items(center);
  @include rem(height, $icon-link-base-height);
  @include rem(width, $icon-link-base-width);

  line-height: $icon-link-base-line-height;

  svg {
    @include rem(height, $icon-link-base-height);
    @include rem(width, $icon-link-base-width);

    fill: $icon-base-link-fill-color;
    stroke: $icon-base-link-stroke-color;
    overflow: hidden;
  }

  &--before {
    @include display(inline);
    @include rem (margin, 0 $icon-link-base-margin-right 0 0);

    svg {
      @include align(bottom);
    }
  }

  &--after {
    @include display(inline);
    @include rem (margin, 0 0 0 $icon-link-base-margin-left);

    svg {
      @include align(bottom);
    }
  }
}

.list-icon {
  @include display(inline-block);
  @include rem(height, auto);
  @include rem(width, auto);

  line-height: $icon-list-base-line-height;

  svg {
    @include rem(height, $icon-list-base-height);
    @include rem(width, $icon-list-base-width);
    @include align(middle);

    fill: $icon-base-list-fill-color;
    stroke: $icon-base-list-stroke-color;
    overflow: hidden;
  }
  //&__disc {
  //  svg {
  //    fill: $icon-base-list-fill-color;
  //  }
  //}
  &--before {
    @include display(inline);
    @include rem (margin, 0 $icon-list-base-margin-right 0 0);
  }

  &--after {
    @include display(inline);
    @include rem (margin, 0 0 0 $icon-list-base-margin-left);
  }
}

.accordion-icon {
  @include display(inline-block);
  @include rem(height, auto);
  @include rem(width, auto);
  @include align(middle);

  line-height: $icon-accordion-base-line-height;

  svg {
    @include rem(height, $icon-accordion-base-height);
    @include rem(width, $icon-accordion-base-width);

    fill: $icon-base-accordion-fill-color;
    stroke: $icon-base-accordion-stroke-color;
    overflow: hidden;
  }

  &--before {
    @include display(inline);
    @include rem (margin, 0 $icon-accordion-base-margin-right 0 0);
  }

  &--after {
    @include display(inline);
    @include rem (margin, 0 0 0 $icon-accordion-base-margin-left);

    position: absolute;
    right: $space-one-fourth;
    top: $space-one-fourth;
    text-align: right;
  }
}

.status-icon {
  @include display(inline-block);
  @include rem(height, auto);
  @include rem(width, auto);
  @include align(middle);
  @include rem(margin, $icon-status-base-margin-top $icon-status-base-margin-right $icon-status-base-margin-bottom $icon-status-base-margin-left);

  line-height: $icon-status-base-line-height;

  svg {
    @include rem(height, $icon-status-base-height);
    @include rem(width, $icon-status-base-width);

    fill: $icon-base-status-fill-color;
    stroke: $icon-base-status-stroke-color;
    overflow: hidden;
  }
}

.close-icon {
  @include display(inline-block);
  @include rem(height, auto);
  @include rem(width, auto);
  @include align(middle);

  line-height: $icon-close-base-line-height;

  svg {
    @include rem(height, $icon-close-base-height);
    @include rem(width, $icon-close-base-width);

    fill: $icon-base-close-fill-color;
    stroke: $icon-base-close-stroke-color;
    overflow: hidden;
  }
}
