/// Mixin - Paragraph
// Usage: @include paragraph()
// custom margin and padding
// @include paragraph($margin: 20px 30px 40px 50px, $padding: 10px 20px 30px 50px, , background-color: $paragraph-base-background-color);

@mixin paragraph($margin-top: $paragraph-base-margin-top, $margin-bottom: $paragraph-base-margin-bottom, $padding: $paragraph-base-padding-top $paragraph-base-padding-right $paragraph-base-padding-bottom $paragraph-base-padding-left, $background-color: $color-background-paragraph, $paragraphcolor: $color-text-paragraph) {
  @include wrappertxtwidth;
  @include rem(padding, $padding);
  @include rem(margin-top, $margin-top);
  @include rem(margin-bottom, $margin-bottom);

  background-color: $background-color;
  color: $paragraphcolor;
}
/// Mixin border-radius
@mixin border-radius($radius: '') {
  @if $radius != 0 {
    border-radius: $radius;
  }
}

/// Mixin hr
//
@mixin hr($border-style: $hr-border-style, $border-width: $hr-border-width, $hrcolor: $color-hr) {
  border-style: $border-style;
  border-width: $border-width;
  color: $hrcolor;
}

/// Mixin - Blockqoute
// Usage: @include blockqoute()
//
// custom margin and padding
// @include paragraph($margin: 20px 30px 40px 50px, $padding: 10px 20px 30px 50px);
// custom margin and padding, backgroundcolor
// @include paragraph($margin: 20px 30px 40px 50px, $padding: 10px 20px 30px 50px, background-color: $blockquote-base-background-color);
//
// @include paragraph($margin: 20px 30px 40px 50px, $padding: 10px 20px 30px 50px, background-color: inherit);

@mixin blockquote($margin: $blockquote-base-margin-top $blockquote-base-margin-right $blockquote-base-margin-bottom $blockquote-base-margin-left, $padding: $blockquote-base-padding-top $blockquote-base-padding-right $blockquote-base-padding-bottom $blockquote-base-padding-left, $background-color: $color-background-blockqoute, $blockquotecolor: $color-text-blockqoute) {
  @include rem(margin, $margin);
  @include rem(padding, $padding);
  @include rem(font-size, $font-size-blockquote);

  font-style: $blockquote-font-style;
  border-left: solid 3px $color-border;
  background-color: $background-color;
  color: $blockquotecolor;
}

/// Mixin - No-bottom
// Usage: @include no-bottom()
@mixin no-bottom {
  margin-bottom: 0;
}

@mixin caption($margin: $caption-base-margin-top $caption-base-margin-right $caption-base-margin-bottom $caption-base-margin-left, $padding: $caption-base-padding-top $caption-base-padding-right $caption-base-padding-bottom $caption-base-padding-left, $background-color: $color-background-caption, $captioncolor: $color-text-caption) {
  @include rem(margin, $margin);
  @include rem(padding, $padding);
  @include rem(font-size, $font-size-caption);

  font-weight: $font-weight-caption;
  font-style: $caption-font-style;
  background-color: $background-color;
  color: $captioncolor;
  text-align: $caption-text-align;
}

/// Mixin - image
@mixin image {
  height: auto;
  max-width: 100%;

  &:focus,
  &:active {
    @include rem(padding, 0);

    color: $color-link-focus;
    background-color: $color-link-base-background-focus;
  }
}

/// Link
// Usage: @include link()
@mixin link {
  color: $color-link-base;
  text-decoration: $text-decoration-link-base;

  &:hover {
    color: $color-link-base-hover;
    text-decoration: $text-decoration-link-base-hover;

    svg {
      fill: $icon-base-link-fill-color-hover;
      stroke: $icon-base-link-stroke-color-hover;
    }
  }

  &:focus,
  &:active {
    color: $color-link-focus;
    background-color: $color-link-base-background-focus;

    svg {
      fill: $icon-base-link-fill-color-hover;
      stroke: $icon-base-link-stroke-color-hover;
    }
  }
  //
  //&:visited {
  //  color: $color-link-base;
  //  text-decoration: $text-decoration-link-base;
  //
  //  &:hover,
  //  &:focus {
