  //    text-decoration: $text-decoration-link-base-hover;
  //  }
  //}
  //
}

/// Mixin - list-reset
// List without margin, padding, and list styles
@mixin list-reset {
  @include rem(margin, 0);
  @include rem(padding, 0);

  list-style: none;
}

/// Mixin - Wrapper
// Outer container mixin for large screens
@mixin wrapper {
  @include clearfix;

  margin: 0 auto;
  max-width: $max-width;
}

/// Mixin text wrapper
@mixin wrappertxtwidth {
  @include clearfix;

  margin: 0 auto;
  max-width: $max-txt-width;
}

/// Mixin - Buttons
@mixin button($bg:$color-1, $color: $color-text, $hoverBg: $color-text, $hoverColor: $white) {
  @include rem(font-size, $font-size-button);
  @include rem(padding, $space-top-button $space-right-button $space-bottom-button $space-left-button);

  line-height: $line-height-button;
  background-color: $bg;
  color: $color;
  cursor: pointer;
  display: inline-block;
  font-weight: $font-weight-button;
  border-style: $border-button;
  border-color: $bg;
  border-width: $border-button-width;
  text-decoration: $text-decoration-button;
  text-align: $text-align-button;
  text-transform: $text-transform-button;

  &:visited {
    color: $color;
  }

  &:hover,
  &[aria-selected="true"] {
    background-color: $hoverBg;
    color: $hoverColor;
    border-style: $border-button;
    border-color: $hoverBg;
    border-width: $border-button-width;
    text-decoration: $text-decoration-button-hover;
  }

  &[aria-selected="true"] {
    &:hover {
      background-color: $bg;
      color: $color;
      border-style: $border-button;
      border-color: $bg;
      border-width: $border-button-width;
    }
  }

  &:focus,
  &[aria-pressed='true'] {
    @include rem(font-size, $font-size-button);
    @include rem(padding, $space-top-button $space-right-button $space-bottom-button $space-left-button);

    background-color: $hoverBg;
    color: $hoverColor;
    line-height: $line-height-button;
    //border: 2px solid $color-border-focus;
    border-style: $border-button;
    border-color: $color-border-focus;
    border-width: $border-button-width;
    // Work around a Firefox/IE bug where the transparent `button` background
    // results in a loss of the default `button` focus styles.
    //
    // Credit: https://github.com/suitcss/base/
    outline: $border-button-width solid $color-border-focus;
    outline: 5px auto -webkit-focus-ring-color;

    &:hover {
      background-color: $bg;
      color: $color;
    }
  }
  // Button with icon
  .button-icon {
    svg {
      fill: $color;
      stroke: $color;
    }
  }

  &:hover,
  &:focus,
  &[aria-pressed='true'] {
    svg {
      fill: $hoverColor;
      stroke: $hoverColor;
    }

    &:focus,
    &[aria-pressed='true'] {
      &:hover {
        svg {
          fill: $color;
          stroke: $color;
        }
      }
    }
  }
}

@mixin button-alt {
  @include button($color-btn-alt-bgrnd, $color-btn-alt-text, $color-btn-alt-bgrnd-hover, $color-btn-alt-text-hover);
  @include rem(padding, $space-top-button-alt $space-right-button-alt $space-bottom-button-alt $space-left-button-alt);

  line-height: $line-height-button-alt;
  font-weight: $font-weight-button-alt;
  text-decoration: $text-decoration-button-alt;
  text-align: $text-align-button-alt;
  text-transform: $text-transform-button-alt;

  &:focus,
  &[aria-pressed='true'] {
    @include rem(padding, $space-top-button-alt $space-right-button-alt $space-bottom-button-alt $space-left-button-alt);

    line-height: $line-height-button-alt;
  }

  .icon {
    @include rem(height, 14px);
    @include align(baseline);
    @include rem(top, 1px);

    position: relative;
    width: auto;

    svg {
      @include rem(height, 14px);

      fill: $color-btn-alt-text;
      stroke: $color-btn-alt-text;
      width: auto;
    }
  }

  &:hover {
    .icon {
      @include rem(height, 14px);
      @include align(baseline);

      width: auto;

      svg {
        @include rem(height, 14px);

        fill: $color-btn-alt-text-hover;
        stroke: $color-btn-alt-text-hover;
        width: auto;
      }
    }
  }
}

@mixin button-alt2 {
  @include button($color-btn-alt2-bgrnd, $color-btn-alt2-text, $color-btn-alt2-bgrnd-hover, $color-btn-alt2-text-hover);
  @include rem(font-size, $font-size-button-alt2);
  @include rem(padding, $space-top-button-alt2 $space-right-button-alt2 $space-bottom-button-alt2 $space-left-button-alt2);

  line-height: $line-height-button-alt2;
  font-weight: $font-weight-button-alt2;
  text-decoration: $text-decoration-button-alt2;
  text-align: $text-align-button-alt2;
  text-transform: $text-transform-button-alt2;

  &:focus,
  &[aria-pressed='true'] {
    @include rem(font-size, $font-size-button-alt2);
    @include rem(padding, $space-top-button-alt2 $space-right-button-alt2 $space-bottom-button-alt2 $space-left-button-alt2);

    line-height: $line-height-button-alt2;
  }

  .icon {
    @include align(baseline);
    @include rem(top, 1px);

    position: relative;

    svg {
      fill: $color-btn-alt2-text;
      stroke: $color-btn-alt2-text;
    }
  }

  &:hover {
    .icon {
      @include align(baseline);

      svg {
        fill: $color-btn-alt2-text-hover;
        stroke: $color-btn-alt2-text-hover;
      }
    }
  }
}

@mixin button-close {
  @include button($color-btn-close-bgrnd, $color-btn-close-text, $color-btn-close-bgrnd-hover, $color-btn-close-text-hover);
  @include rem(font-size, $font-size-button-close);
  @include rem(padding, $space-top-button-close $space-right-button-close $space-bottom-button-close $space-left-button-close);

  line-height: $line-height-button-close;
  font-weight: $font-weight-button-close;
  text-decoration: $text-decoration-button-close;
  text-align: $text-align-button-close;
  text-transform: $text-transform-button-close;

  &:focus,
  &[aria-pressed='true'] {
    @include rem(font-size, $font-size-button-close);
    @include rem(padding, $space-top-button-close $space-right-button-close $space-bottom-button-close $space-left-button-close);

    line-height: $line-height-button-close;
  }

  &:hover {
    background-color: $transparent;
    border-color: $transparent;

    svg {
      fill: white;
      stroke: white;
    }
  }
}

@mixin button-close-invert {
  @include button($color-btn-close-invert-bgrnd, $color-btn-close-invert-text, $color-btn-close-invert-bgrnd-hover, $color-btn-close-invert-text-hover);
  @include rem(font-size, $font-size-button-close);
  @include rem(padding, $space-top-button-close $space-right-button-close $space-bottom-button-close $space-left-button-close);

  line-height: $line-height-button-close;
  font-weight: $font-weight-button-close;
  text-decoration: $text-decoration-button-close;
  text-align: $text-align-button-close;
  text-transform: $text-transform-button-close;

  &:focus,
  &[aria-pressed='true'] {
    @include rem(font-size, $font-size-button-close);
    @include rem(padding, $space-top-button-close $space-right-button-close $space-bottom-button-close $space-left-button-close);

    line-height: $line-height-button-close;
  }

  svg {
    fill: $white !important;
    stroke: $white !important;
  }

  &:hover {
    background-color: $transparent;
    border-color: $transparent;

    svg {
      fill: $color-1 !important;
      stroke: $color-1 !important;
    }
  }
}
/// Mixin Tags
@mixin tag {
  @include button($color-tag-bgrnd, $color-tag-text, $color-tag-bgrnd-hover, $color-tag-text-hover);
  @include rem(padding, $space-top-tag $space-right-tag $space-bottom-tag $space-left-tag);

  line-height: $line-height-tag;
  font-weight: $font-weight-tag;
  text-decoration: $text-decoration-tag;
  text-align: $text-align-tag;
  text-transform: $text-transform-tag;
  font-size: $font-size-tag;
  border-radius: $tag-border-radius;

  &:focus {
    @include rem(padding, $space-top-tag $space-right-tag $space-bottom-tag $space-left-tag);

    line-height: $line-height-tag;
  }
}

/// Mixin - Transition
@mixin transition($transition-property, $transition-time, $method) {
  -webkit-transition: $transition-property $transition-time $method;
  -moz-transition: $transition-property $transition-time $method;
  -ms-transition: $transition-property $transition-time $method;
  -o-transition: $transition-property $transition-time $method;
  transition: $transition-property $transition-time $method;
}

/// Mixin - Standard Margin bottom
@mixin margin-bottom {
  @include rem(margin-bottom, 10px);
}

/// Mixin - Rem to PX see: https://github.com/pierreburel/sass-rem
// Usage:   @include rem(xyz, 10px)
$rem-baseline: $font-size-base !default;
$rem-fallback: false !default;
$rem-px-only: false !default;

@function rem-separator($list, $separator: false) {
  @if $separator == "comma" or $separator == "space" {
    @return append($list, null, $separator);
  }

  @if function-exists("list-separator") == true {
    @return list-separator($list);
  }

  // list-separator polyfill by Hugo Giraudel (https://sass-compatibility.github.io/#list_separator_function)
  $test-list: ();

  @each $item in $list {
    $test-list: append($test-list, $item, space);
  }

  @return if($test-list == $list, space, comma);
}

@mixin rem-baseline($zoom: 100%) {
  font-size: $zoom / 16px * $rem-baseline;
}

@function rem-convert($to, $values...) {
  $result: ();
  $separator: rem-separator($values);

  @each $value in $values {
    @if type-of($value) == "number" and unit($value) == "rem" and $to == "px" {
      $result: append($result, $value / 1rem * $rem-baseline + 0px, $separator);
    }

    @else if type-of($value) == "number" and unit($value) == "px" and $to == "rem" {
      $result: append($result, $value / $rem-baseline + 0rem, $separator);
    }

    @else if type-of($value) == "list" {
      $value-separator: rem-separator($value);
      $value: rem-convert($to, $value...);
      $value: rem-separator($value, $value-separator);
      $result: append($result, $value, $separator);
    }

    @else {
      $result: append($result, $value, $separator);
    }
  }

  @return if(length($result) == 1, nth($result, 1), $result);
}

@function rem($values...) {
  @if $rem-px-only {
    @return rem-convert(px, $values...);
  }

  @else {
    @return rem-convert(rem, $values...);
  }
}

@mixin rem($properties, $values...) {
  @if type-of($properties) == "map" {
    @each $property in map-keys($properties) {
      @include rem($property, map-get($properties, $property));
    }
  }

  @else {
    @each $property in $properties {
      @if $rem-fallback or $rem-px-only {
        #{$property}: rem-convert(px, $values...);
      }

      @if not $rem-px-only {
        #{$property}: rem-convert(rem, $values...);
      }
    }
  }
}

/// Mixin Contrast
//
// see: components/_patterns/00-base/global/01-colors/_colors-used.scss
// see: components/_patterns/00-base/global/01-colors/_color-vars.scss
// Usage: @include contrast(classname); - classname is the $element
// f.E.: @include contrast(heading); -- this is for .heading
//
// For every "value" in "$contrastcolor"
// the output will be:
//
// .blackwhite .heading {
//  color: #000;
//  background-color: #fff;
// }
// .yellowblue .heading {
//  color: #ff0;
//  background-color: #00f;
// }
// etc ...

@mixin contrast($element) {
  @each $name, $value in $contrastcolors {
    .#{$name} {
      .#{$element} {
        // green black
        @if ($name) == ('greenblack') {
          color: $contrast-greenblack;
          background-color: $contrast-background-greenblack;

          &__link {
            color: $contrast-greenblack;

            &:hover,
            &:focus {
              color: $contrast-background-greenblack;
              background-color: $contrast-greenblack;
            }
          }
        }
        // redblue
        @else if ($name) == ('redblue') {
          color: $contrast-redblue;
          background-color: $contrast-background-redblue;

          &__link {
            color: $contrast-redblue;

            &:hover,
            &:focus {
              color: $contrast-background-redblue;
              background-color: $contrast-redblue;
            }
          }
        }
        // "default"
        @else {
          color: $value;
          background-color: invert($value);

          &__link {
            color: $value;

            &:hover,
            &:focus {
              color: invert($value);
              background-color: $value;
            }
          }
        }
      }
    }
  }
}
