// _headings.scss
// Generic header styles:
// All arguments are optional. If not defined, the defaults are used

/// Mixin - Headings
@mixin heading($font-family: $font-family-heading, $font-size: $font-size-base, $line-height: $line-height-heading, $color: $color-heading, $color-link: $color-heading-link, $color-link-hover: $color-heading-link-hover, $font-weight: $heading-weight) {
  color: #{$color};
  font-family: #{$font-family};
  font-weight: #{$font-weight};
  font-size: #{$font-size};
  line-height: #{$line-height};

  @include rem(margin, $heading-margin-top $heading-margin-right $heading-margin-bottom $heading-margin-left);

  width: auto;

  &--link {
    .link {
      color: $color-heading-link;

      &:hover {
        color: $color-heading-link-hover;
      }
    }
  }
  // modifier
  //&--modifier {
  //  background: red;
  //}
  &--no-bottom {
    @include rem(margin-bottom, 0);
  }
}

h1 {
  @include heading($font-size: $xxlarge);
}

h2 {
  @include heading($font-size: $xlarge);
}

h3 {
  @include heading($font-size: $large);
}

h4 {
  @include heading($font-size: $medium);
}

h5 {
  @include heading($font-size: $small);
}

h6 {
  @include heading($font-size: $small);
}

.heading {
  @include heading();

  &__font-size-base {
    @include rem(font-size, $font-size-base);
  }

  &__xsmall {
    @include rem(font-size, $xsmall);
  }

  &__small {
    @include rem(font-size, $small);
  }

  &__medium {
    @include rem(font-size, $medium);
  }

  &__large {
    @include rem(font-size, $large);
  }

  &__xlarge {
    @include rem(font-size, $xlarge);
  }

  &__xxlarge {
    @include rem(font-size, $xxlarge);
  }
}
