// _textfield.scss

.form-item__textfield {
  border: $border-form-width $border-form $color-border-form;
  //background-color: $color-form-item-input-background;
  &:focus,
  &:active {
    border-color: $color-border-form-focus !important;
    border-style: $border-button;
    border-width: $border-form-active-width;
  }

  &:hover {
    border-color: $color-border-form-hover;
  }

  &::-webkit-input-placeholder {
    color: $color-placeholder;
  }

  &:-moz-placeholder {
    color: $color-placeholder;
  }

  &::-moz-placeholder {
    color: $color-placeholder;
  }

  &:-ms-input-placeholder {
    color: $color-placeholder;
  }

  // if the field is required
  &--required {
    border-color: $color-border-form-required;
    background-color: $color-form-input-required-background;
  }
}

.form-item--textfield:focus,
.form-item--textfield:hover,
.form-item--textfield:active {
  svg {
    fill: $color-icon-form-focus;
  }

  > .form-item__textfield {
    border-color: $color-border-form-hover;
  }
}

// success
.form-item--success {
  .form-item__textfield {
    border-color: $color-form-success;
    //background-color: $color-form-success-background;
  }
}

// error
.form-item--error {
  .form-item__textfield {
    border-color: $color-form-error;
    background-color: $color-form-input-error-background;
  }

  &:hover {
    .form-item__textfield {
      border-color: $color-border-form;
      background-color: $color-form-error-background;
    }

    svg,
    path {
      //stroke: $color-icon-form-hover;
      fill: $color-icon-form;
    }
  }
}

// required error
.form-item--required-error {
  .form-item__textfield {
    border-color: $color-form-required-error;
    background-color: $color-form-required-error-background;
  }

  &:hover {
    .form-item__textfield {
      border-color: $color-border-form;
      background-color: $color-form-required-error-background;
    }

    svg,
    path {
      //stroke: $color-icon-form-hover;
      fill: $color-icon-form;
    }
  }
}
