// _visibility.scss

/// Mixin -  Visibility

@mixin visibility($visibility: null) {
  visibility: $visibility !important;
}

.visible {
  @include visibility(visible);
}

.invisible {
  @include visibility(hidden);
}

/// Mixin - Screen reader only - invisible element but available to screen readers.
// See: http://a11yproject.com/posts/how-to-hide-content/
// See: https://hugogiraudel.com/2016/10/13/css-hide-and-seek/

@mixin sr-only {
  @include rem(padding, 0);

  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  clip-path: inset(50%);
  border: 0;
}

// Use this in combination with .sr-only
// Content will only be display when it's focused.
//
// see https://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1

@mixin sr-only-focus {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    overflow: visible;
    clip: auto;
    white-space: normal;
    clip-path: none;
  }
}

.sr-only {
  @include sr-only;
}

.sr-only-focus {
  @include sr-only;
  @include sr-only-focus;
}
